<template>
	<div class="header">
		<div class="middle">
			<div class="logo">
				<a href="index.html"> <img src="../assets/images/logo.png" /> </a>
			</div>
			<div class="nav">
				{{ setActive() }}
				<ul>
					<li :class="[isActive==0?'active':'']">
						<router-link to="index"></router-link>
					</li>
					<li :class="[isActive==1?'active':'']">
						<router-link to="process"></router-link>
					</li>
					<li :class="[isActive==2?'active':' ']">
						<router-link to="case"></router-link>
					</li>
					<li :class="[isActive==3?'active':' ']">
						<router-link to="news"></router-link>
					</li>
					<li :class="[isActive==4?'active':' ']">
						<router-link to="packages"></router-link>
					</li>
					<li :class="[isActive==5?'active':' ']" @click="getActive(5)">
						<router-link to="details"></router-link>
					</li>
					<li>
						<router-link to=""></router-link>
					</li>
				</ul>
			</div>
		</div>
		<div class="banner">
			<div class="box">
				<ul class="ad">
					<li>
						<a><img src="../assets/images/banner1.jpg" /></a>
					</li>
					<li>
						<a><img src="../assets/images/banner2.jpg" /></a>
					</li>
					<li>
						<a><img src="../assets/images/banner3.jpg" /></a>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'header',
		data() {
			return {
				isActive: 0
			}
		},
		methods: {
			setActive (){
				var n,curURL = location.href.split( "#/")[1];
				if( curURL == "process" ){
					n = 1
				}else if( curURL == "case" ) {
					n = 2;
				}else if( curURL == "news" ) {
					n = 3;
				}else if( curURL == "packages" ) {
					n = 4;
				}else if( curURL == "details" ) {
					n = 5;
				}else {
					n = 0;
				}
				this.$data.isActive = n;
			}
		},
		mounted: function(){
			$(".header .banner").Banner({
				"pagination":true,
				"autoPlay":true,
				"speed":600,
				"delay":3000,	
				"buttons":false
			});
			$(".header .nav ul li").click(function(){
				$(this).addClass("active").siblings().removeClass("active");
			})
		}
	}
</script>


